export default {
name:'customer',
components:{
XNumber,
Group,
Cell,
InlineXNumber,
XInput,
NavBar,
Scroller
},
data(){
return{
roundValue: 0,
dateTime:'',
serviceName:'',
money:'',
weekDate:'',
totalPrice:'',
username:'',
phone:'',
appointId:'',
docmHeight: '0', //
showHeight: '0', //
hideshow:true, //footer,
isResize:false, //
