}
},
watch:{
roundValue(newName,oldName){
console.log('',newName)
this.totalPrice = newName * this.money
},











},
created(){

},
mounted(){

this.getToCustomer();

this.showWeek();
















},
methods:{

getToCustomer(){
var time = this.$route.query.dateTime;
var name = this.$route.query.servieName;
