var date = this.$route.query.date;
var id   = this.$route.query.appointId;
console.log("customer----->>>",time,name,yuan,date,id)
this.dateTime = time;
this.serviceName = name;
this.money = yuan;
this.weekDate = date;
this.appointId = id;





},

getHeight(){



console.log(":"+document.documentElement.clientHeight)
console.log(":"+document.body.clientHeight)
this.buttomHeight = this.$refs.buttomOrder.$el.offsetTop  
console.log("buttomHeight",this.buttomHeight)
console.log("docmHeight:",this.docmHeight)
console.log("showHeight:",this.showHeight)
},
lostfocus(){
console.log("")
if(this.hideshow == false){
this.hideshow = true
}
},
getFocus(){
console.log("")
if(this.hideshow == true){
this.hideshow = false
}
},
changeNum(val){
this.roundValue = val;
console.log('changeNum',this.roundValue)
},


showWeek(){
var str = this.$route.query.date;
console.log("customer "+str)

var calendarObject = new Date(str)
var day = calendarObject.getDay()
if(day == 1){
this.calendarWeek = '';
console.log('',day);
}
if(day == 2){
this.calendarWeek = '';
console.log('',day);
}
if(day == 3){

this.calendarWeek = '';
console.log('',day);
}
if(day == 4){
this.calendarWeek = '';
console.log('',day);
}
if(day == 5){
this.calendarWeek = '';
console.log('',day);
}
if(day == 6){
this.calendarWeek = '';
console.log('',day);
}
if(day == 0){
this.calendarWeek = '';
}
}
}
}




